import { useState, useEffect, useRef } from 'react';
import { CAMERA_SETTINGS } from '../config/constants';

export const useCamera = () => {
  const videoRef = useRef(null);
  const [stream, setStream] = useState(null);
  const [facingMode, setFacingMode] = useState(CAMERA_SETTINGS.FACING_MODES.ENVIRONMENT);
  const [error, setError] = useState(null);

  const startCamera = async () => {
    try {
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }

      const newStream = await navigator.mediaDevices.getUserMedia({
        video: {
          facingMode,
          width: { ideal: CAMERA_SETTINGS.IDEAL_WIDTH },
          height: { ideal: CAMERA_SETTINGS.IDEAL_HEIGHT }
        }
      });

      setStream(newStream);
      if (videoRef.current) {
        videoRef.current.srcObject = newStream;
      }
      setError(null);
    } catch (err) {
      setError('Không thể truy cập camera');
      console.error('Camera access error:', err);
    }
  };

  const switchCamera = () => {
    setFacingMode(prev => 
      prev === CAMERA_SETTINGS.FACING_MODES.USER 
        ? CAMERA_SETTINGS.FACING_MODES.ENVIRONMENT 
        : CAMERA_SETTINGS.FACING_MODES.USER
    );
  };

  const stopCamera = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
      setStream(null);
    }
  };

  useEffect(() => {
    startCamera();
    return () => {
      stopCamera();
    };
  }, [facingMode]);

  return {
    videoRef,
    stream,
    error,
    facingMode,
    startCamera,
    stopCamera,
    switchCamera
  };
};