import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useToast } from '../components/shared/Toast';
import { useLoading } from '../components/shared/Loading';
import { Header } from '../components/layout/Header';
import { LoginForm } from '../components/features/LoginForm';
import { CameraCapture } from '../components/features/CameraCapture';
import { Modal } from '../components/shared/Modal';
import { AttendancePreview } from '../components/features/AttendancePreview';
import { Xinnghiphep } from '../components/features/Xinnghiphep';
import { LeaveHistory } from '../components/features/LeaveHistory';
import { AttendanceHistory } from '../components/features/AttendanceHistory';
import { AdminAttendanceHistory } from '../components/features/AdminAttendanceHistory';
import { LeaveApproval } from '../components/features/LeaveApproval';
import { apiService } from '../services/api';
import CompanySelector from '../components/features/CompanySelector';
import moment from 'moment-timezone';

export const HomePage = () => {
    const navigate = useNavigate();
    const { user, logout } = useAuth();
    const { showToast } = useToast();
    const { setLoading } = useLoading();
    const [view, setView] = useState('menu');
    const [checkType, setCheckType] = useState(null);
    const [modal, setModal] = useState({ isOpen: false, message: '', onConfirm: null });
    const [previewData, setPreviewData] = useState(null);
    const [note, setNote] = useState('');
    const [selectedCompany, setSelectedCompany] = useState(null);
    // Kiểm tra và yêu cầu quyền khi component mount
    useEffect(() => {
        const checkPermissions = async () => {
            try {
                await navigator.mediaDevices.getUserMedia({ video: true });
                await new Promise((resolve) => {
                    navigator.geolocation.getCurrentPosition(resolve, resolve);
                });
            } catch (error) {
                showToast('Vui lòng cấp quyền camera và vị trí để sử dụng ứng dụng', 'error');
            }
        };

        if (user) {
            checkPermissions();
        }
    }, [user]);

    const handleLogout = () => {
        setLoading({ isLoading: true, message: 'Đang đăng xuất...' });
        setTimeout(() => {
            logout();
            showToast('Đăng xuất thành công');
            setLoading({ isLoading: false, message: '' });
        }, 1000);
    };
    const handleResetPage = () => {
        window.location.reload();
    };
    const handleCheckInOut = async (captureData) => {
        setLoading({ isLoading: true, message: 'Đang xử lý...' });

        try {
            const currentTime = moment().tz('Asia/Ho_Chi_Minh');
            const date = currentTime.format('DD/MM/YYYY');
            const timeStr = currentTime.format('HH:mm:ss');
            const idChamCong = `${user['Họ và Tên']}${date}`;

            const existingRecord = await apiService.checkExistingRecord(idChamCong);

            const newData = {
                "ID CV": idChamCong,
                "NGÀY": currentTime.format('YYYY-MM-DD'),
                "Họ và Tên": user['Họ và Tên'],
                "GHI CHÚ": captureData.note,
                [checkType === 'in' ? "CÔNG TY VÀO" : "CÔNG TY RA"]: selectedCompany.ID_congty
            };

            if (checkType === 'in') {
                if (existingRecord?.[0]?.IN) {
                    setModal({
                        isOpen: true,
                        message: `Bạn đã check-in lúc ${existingRecord[0].IN}. Bạn có muốn check-in lại không?`,
                        onConfirm: () => processCheckInOut(newData, captureData, existingRecord[0])
                    });
                    setLoading({ isLoading: false, message: '' });
                    return;
                }
                newData["IN"] = timeStr;
                newData["HÌNH ẢNH IN"] = captureData.imageUrl;
                newData["ĐỊA CHỈ IN"] = `${captureData.location.latitude},${captureData.location.longitude}`;
                if (existingRecord?.[0]?.OUT) {
                    newData["OUT"] = existingRecord[0].OUT;
                    newData["HÌNH ẢNH OUT"] = existingRecord[0]["HÌNH ẢNH OUT"];
                    newData["ĐỊA CHỈ OUT"] = existingRecord[0]["ĐỊA CHỈ OUT"];
                }
            } else {
                if (existingRecord?.[0]?.OUT) {
                    setModal({
                        isOpen: true,
                        message: `Bạn đã check-out lúc ${existingRecord[0].OUT}. Bạn có muốn check-out lại không?`,
                        onConfirm: () => processCheckInOut(newData, captureData, existingRecord[0])
                    });
                    setLoading({ isLoading: false, message: '' });
                    return;
                }
                if (existingRecord?.[0]?.IN) {
                    newData["IN"] = existingRecord[0].IN;
                    newData["HÌNH ẢNH IN"] = existingRecord[0]["HÌNH ẢNH IN"];
                    newData["ĐỊA CHỈ IN"] = existingRecord[0]["ĐỊA CHỈ IN"];
                }
                newData["OUT"] = timeStr;
                newData["HÌNH ẢNH OUT"] = captureData.imageUrl;
                newData["ĐỊA CHỈ OUT"] = `${captureData.location.latitude},${captureData.location.longitude}`;
            }

            await processCheckInOut(newData, captureData, existingRecord?.[0]);
        } catch (error) {
            console.error('Check in/out error:', error);
            showToast('Đã có lỗi xảy ra khi xử lý chấm công', 'error');
        } finally {
            setLoading({ isLoading: false, message: '' });
        }
    };

    const processCheckInOut = async (newData, captureData, existingRecord) => {
        try {
            setLoading({ isLoading: true, message: 'Đang lưu thông tin...' });

            if (existingRecord) {
                if (checkType === 'in') {
                    const currentTime = moment().format('DD/MM/YYYY HH:mm:ss');
                    newData["IN"] = moment().format('HH:mm:ss');
                    newData["HÌNH ẢNH IN"] = captureData.imageUrl;
                    newData["ĐỊA CHỈ IN"] = `${captureData.location.latitude},${captureData.location.longitude}`;
                    newData["LỊCH SỬ IN"] = `${existingRecord["LỊCH SỬ IN"] || ""}\n${user['Họ và Tên']} Đã check in lại vào ngày: (${currentTime}) Ghi chú: ${captureData.note}`;

                    if (existingRecord.OUT) {
                        newData["OUT"] = existingRecord.OUT;
                        newData["HÌNH ẢNH OUT"] = existingRecord["HÌNH ẢNH OUT"];
                        newData["ĐỊA CHỈ OUT"] = existingRecord["ĐỊA CHỈ OUT"];
                        newData["LỊCH SỬ OUT"] = existingRecord["LỊCH SỬ OUT"];
                    }
                } else if (checkType === 'out') {
                    const currentTime = moment().format('DD/MM/YYYY HH:mm:ss');
                    newData["OUT"] = moment().format('HH:mm:ss');
                    newData["HÌNH ẢNH OUT"] = captureData.imageUrl;
                    newData["ĐỊA CHỈ OUT"] = `${captureData.location.latitude},${captureData.location.longitude}`;
                    newData["LỊCH SỬ OUT"] = `${existingRecord["LỊCH SỬ OUT"] || ""}\n${user['Họ và Tên']} Đã check out lại vào: (${currentTime}) Ghi chú: ${captureData.note}`;

                    if (existingRecord.IN) {
                        newData["IN"] = existingRecord.IN;
                        newData["HÌNH ẢNH IN"] = existingRecord["HÌNH ẢNH IN"];
                        newData["ĐỊA CHỈ IN"] = existingRecord["ĐỊA CHỈ IN"];
                        newData["LỊCH SỬ IN"] = existingRecord["LỊCH SỬ IN"];
                    }
                }
            }

            const response = await apiService.request('LICH CV', existingRecord ? 'Edit' : 'Add', {
                Rows: [newData]
            });

            if (response?.Rows?.[0]) {
                setModal({ isOpen: false, message: '', onConfirm: null });
                showToast(`Chấm công ${checkType === 'in' ? 'vào' : 'ra'} thành công!`);
                setPreviewData({
                    ...captureData,
                    type: checkType
                });
            } else {
                throw new Error('Invalid response format');
            }
        } catch (error) {
            console.error('Save attendance error:', error);
            showToast('Lưu thông tin thất bại', 'error');
        } finally {
            setLoading({ isLoading: false, message: '' });
        }
    };
    // Add this handler
    const handleCompanySelect = (company) => {
        setSelectedCompany(company);
        setView('camera');
    };
    // Handler for starting check in process
    const handleStartCheckIn = () => {
        setCheckType('in');
        setView('company-selection');
    };

    // Handler for starting check out process
    const handleStartCheckOut = () => {
        setCheckType('out');
        setView('company-selection');
    };

    return (
        <div className="min-h-screen bg-gray-100">
            <Header />

            <main className="max-w-7xl mx-auto px-4 py-6 sm:px-6 lg:px-8">
                {!user ? (
                    <LoginForm />
                ) : (
                    <>
                        {view === 'menu' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-md">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10 space-y-4">
                                    <button
                                        onClick={handleStartCheckIn}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700"
                                    >
                                        <span className="flex items-center">
                                            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7" />
                                            </svg>
                                            Check In
                                        </span>
                                    </button>

                                    <button
                                        onClick={handleStartCheckOut}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-600 hover:bg-green-700"
                                    >
                                        <span className="flex items-center">
                                            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" />
                                            </svg>
                                            Check Out
                                        </span>
                                    </button>

                                    <button
                                        onClick={() => setView('/xinnghiphep')}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-purple-600 hover:bg-purple-700"
                                    >
                                        Đơn xin nghỉ phép
                                    </button>

                                    <button
                                        onClick={() => setView('history')}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700"
                                    >
                                        <span className="flex items-center">
                                            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                                            </svg>
                                            Xem lịch sử chấm công
                                        </span>
                                    </button>
                                    {user && user['Họ và Tên'] === 'LÊ THANH TƯỜNG' && (
                                        <button
                                            onClick={() => setView('admin-attendance')}
                                            className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-purple-600 hover:bg-purple-700"
                                        >
                                            Thống kê nhân viên
                                        </button>
                                    )}
                                    <button
                                        onClick={() => setView('leave-history')}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-yellow-600 hover:bg-yellow-700"
                                    >
                                        <span className="flex items-center">
                                            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                                            </svg>
                                            Xem lịch sử nghỉ phép
                                        </span>
                                    </button>
                                    {user && user['Họ và Tên'] === 'LÊ THANH TƯỜNG' && (
                                        <button
                                            onClick={() => setView('leave-approval')}
                                            className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-orange-600 hover:bg-orange-700"
                                        >
                                            <span className="flex items-center">
                                                <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                                                </svg>
                                                Duyệt đơn nghỉ phép
                                            </span>
                                        </button>
                                    )}


                                    <button
                                        onClick={handleLogout}
                                        className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700"
                                    >
                                        <span className="flex items-center">
                                            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
                                            </svg>
                                            Đăng xuất
                                        </span>
                                    </button>
                                    {/* Fixed reset button */}
                                    <button
                                        onClick={handleResetPage}
                                        className="fixed bottom-6 right-6 p-3 bg-gray-600 hover:bg-gray-700 text-white rounded-full shadow-lg transition-all duration-300 hover:scale-110 z-50"
                                        title="Làm mới trang"
                                    >
                                        <svg
                                            className="w-6 h-6"
                                            fill="none"
                                            stroke="currentColor"
                                            viewBox="0 0 24 24"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                strokeWidth="2"
                                                d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                                            />
                                        </svg>
                                    </button>
                                </div>
                            </div>
                        )}

                        {view === 'camera' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-md">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <CameraCapture
                                        type={checkType}
                                        onCapture={handleCheckInOut}
                                        onBack={() => setView('menu')}
                                    />
                                </div>
                            </div>
                        )}

                        {view === 'history' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-5xl">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <AttendanceHistory onBack={() => setView('menu')} />
                                </div></div>
                        )}
                        {view === 'leave-history' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-5xl">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <LeaveHistory onBack={() => setView('menu')} />
                                </div>
                            </div>
                        )}
                        {view === 'company-selection' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-md">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <CompanySelector
                                        type={checkType}
                                        onSelect={handleCompanySelect}
                                        onBack={() => setView('menu')}
                                    />
                                </div>
                            </div>
                        )}

                        {view === 'leave-approval' && (
                            <div className="sm:mx-auto sm:w-full sm:max-w-7xl">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <LeaveApproval onBack={() => setView('menu')} />
                                </div>
                            </div>
                        )}
                        {view === '/xinnghiphep' && (
                            <div className="sm:mx-auto sm:w-full ">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <Xinnghiphep onBack={() => setView('menu')} />
                                </div>
                            </div>
                        )}

                        {view === 'admin-attendance' && (
                            <div className="sm:mx-auto sm:w-full ">
                                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                                    <AdminAttendanceHistory onBack={() => setView('menu')} />
                                </div>
                            </div>
                        )}
                    </>
                )}
            </main>

            <Modal
                isOpen={modal.isOpen}
                title="Xác nhận ghi đè"
                message={modal.message}
                onConfirm={modal.onConfirm}
                onClose={() => setModal({ isOpen: false, message: '', onConfirm: null })}
            />

            {previewData && (
                <AttendancePreview
                    data={previewData}
                    onClose={() => {
                        setPreviewData(null);
                        setView('menu');
                    }}
                />
            )}
        </div>
    );
};

export default HomePage;