import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { clsx } from 'clsx';
import { twMerge } from 'tailwind-merge';

// Utils function
const cn = (...inputs) => {
    return twMerge(clsx(inputs));
};

// Card Components
const Card = React.forwardRef(({ className, ...props }, ref) => (
    <div
        ref={ref}
        className={cn(
            "rounded-lg border bg-card text-card-foreground shadow-sm",
            className
        )}
        {...props}
    />
));
Card.displayName = "Card";

// Constants for AppSheet API
const APP_IDHCNS = 'c99c8a08-b6cf-4a71-89e7-2b913c7f45be';
const ACCESS_KEYHCNS = 'V2-tKXHx-Qri8Z-oevxi-062rn-BGIWj-7eYMM-Uo9nY-USAAM';
const REGION = 'www';

export const Xinnghiphep = ({ onBack }) => {
    const { user } = useAuth();
    const [formData, setFormData] = useState({
        dienNghi: '',
        nghiTu: '',
        nghiDen: '',
        lyDoNghi: '',
        nhanVienNhanBanGiao: '',
        soGioNghi: '',
        fileHS: null,
        chucVu: '',
        phongBan: ''
    });

    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState({ type: '', text: '' });

    // Load employee data on component mount
    useEffect(() => {
        loadEmployeeInfo();
    }, []);

    const loadEmployeeInfo = async () => {
        try {
            const response = await apiRequesthcns('HCNS_HSNS', 'Find');
            const employeeInfo = response.find(item => item.KEY === user.KEY);
            
            if (employeeInfo) {
                setFormData(prev => ({
                    ...prev,
                    chucVu: employeeInfo["Chức vụ"],
                    phongBan: employeeInfo["Phòng ban"]
                }));
            }
        } catch (error) {
            console.error('Failed to load employee info:', error);
            setMessage({ type: 'error', text: 'Không thể tải thông tin nhân viên' });
        }
    };

    const apiRequesthcns = async (tableName, action, data = {}) => {
        const apiUrl = `https://${REGION}.appsheet.com/api/v2/apps/${APP_IDHCNS}/tables/${tableName}/Action`;
        try {
            const response = await fetch(apiUrl, {
                method: 'POST',
                headers: {
                    'ApplicationAccessKey': ACCESS_KEYHCNS,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    Action: action,
                    Properties: {
                        Locale: 'vi-VN',
                        Timezone: 'Asia/Ho_Chi_Minh'
                    },
                    ...data
                })
            });
            return await response.json();
        } catch (error) {
            throw error;
        }
    };

    const handleInputChange = (e) => {
        const { name, value, files } = e.target;
        if (name === 'fileHS' && files) {
            setFormData(prev => ({ ...prev, [name]: files[0] }));
        } else {
            setFormData(prev => ({ ...prev, [name]: value }));
        }
    };

    const readFile = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
            reader.readAsDataURL(file);
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        try {
            let filebase = "";
            if (formData.fileHS) {
                filebase = await readFile(formData.fileHS);
            }

            const leaveData = {
                "KEY": user.KEY,
                "DIỆN NGHỈ": formData.dienNghi,
                "NGHỈ TỪ": formData.nghiTu,
                "NGHỈ ĐẾN": formData.nghiDen,
                "LÝ DO NGHỈ": formData.lyDoNghi,
                "NHÂN VIÊN NHẬN BÀN GIAO VIỆC": formData.nhanVienNhanBanGiao,
                "ĐƠN ĐÍNH KÈM": filebase,
                "Số giờ nghỉ": formData.soGioNghi
            };

            await apiRequesthcns('Nghi phep', 'Add', { Rows: [leaveData] });
            setMessage({ type: 'success', text: 'Đơn đã được gửi thành công!' });
            setFormData({
                dienNghi: '',
                nghiTu: '',
                nghiDen: '',
                lyDoNghi: '',
                nhanVienNhanBanGiao: '',
                soGioNghi: '',
                fileHS: null,
                chucVu: formData.chucVu,
                phongBan: formData.phongBan
            });
        } catch (error) {
            console.error('Error submitting form:', error);
            setMessage({ type: 'error', text: 'Có lỗi xảy ra khi gửi đơn' });
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="space-y-6">
            <Card className="p-6">
                <h2 className="text-2xl font-bold text-center text-indigo-700 mb-6">Đơn xin nghỉ phép</h2>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium mb-1">Họ và tên</label>
                        <input
                            type="text"
                            value={user["Họ và Tên"]}
                            readOnly
                            className="w-full p-2 border rounded-md bg-gray-50"
                        />
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm font-medium mb-1">Chức vụ</label>
                            <input
                                type="text"
                                value={formData.chucVu}
                                readOnly
                                className="w-full p-2 border rounded-md bg-gray-50"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium mb-1">Phòng ban</label>
                            <input
                                type="text"
                                value={formData.phongBan}
                                readOnly
                                className="w-full p-2 border rounded-md bg-gray-50"
                            />
                        </div>
                    </div>

                    <div>
                        <label className="block text-sm font-medium mb-1">Diện nghỉ</label>
                        <select
                            name="dienNghi"
                            value={formData.dienNghi}
                            onChange={handleInputChange}
                            className="w-full p-2 border rounded-md"
                        >
                            <option value="">Chọn diện nghỉ</option>
                            <option value="Nghỉ hưởng BHXH có chứng từ">Nghỉ hưởng BHXH có chứng từ</option>
                            <option value="Nghỉ phép năm">Nghỉ phép năm</option>
                            <option value="Nghỉ chế độ theo luật có chứng từ">Nghỉ chế độ theo luật có chứng từ</option>
                            <option value="Nghỉ việc riêng không hưởng lương">Nghỉ việc riêng không hưởng lương</option>
                            <option value="Nghỉ khác">Nghỉ khác</option>
                        </select>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm font-medium mb-1">Nghỉ từ</label>
                            <input
                                type="date"
                                name="nghiTu"
                                value={formData.nghiTu}
                                onChange={handleInputChange}
                                className="w-full p-2 border rounded-md"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium mb-1">Nghỉ đến</label>
                            <input
                                type="date"
                                name="nghiDen"
                                value={formData.nghiDen}
                                onChange={handleInputChange}
                                className="w-full p-2 border rounded-md"
                            />
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm font-medium mb-1">Số giờ nghỉ</label>
                            <input
                                type="number"
                                name="soGioNghi"
                                value={formData.soGioNghi}
                                onChange={handleInputChange}
                                className="w-full p-2 border rounded-md"
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium mb-1">Bàn giao việc</label>
                            <input
                                type="text"
                                name="nhanVienNhanBanGiao"
                                value={formData.nhanVienNhanBanGiao}
                                onChange={handleInputChange}
                                className="w-full p-2 border rounded-md"
                            />
                        </div>
                    </div>

                    <div>
                        <label className="block text-sm font-medium mb-1">Lý do nghỉ</label>
                        <textarea
                            name="lyDoNghi"
                            value={formData.lyDoNghi}
                            onChange={handleInputChange}
                            rows="3"
                            className="w-full p-2 border rounded-md"
                        />
                    </div>

                    <div>
                        <label className="block text-sm font-medium mb-1">Đơn đính kèm</label>
                        <input
                            type="file"
                            name="fileHS"
                            onChange={handleInputChange}
                            className="w-full p-2 border rounded-md"
                        />
                    </div>

                    {message.text && (
                        <div className={`p-3 rounded-md ${message.type === 'success' ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                            {message.text}
                        </div>
                    )}

                    <div className="flex gap-4">
                        <button
                            type="button"
                            onClick={onBack}
                            className="w-full py-2 px-4 bg-gray-600 text-white rounded-md hover:bg-gray-700"
                        >
                            Quay lại
                        </button>
                        <button
                            type="submit"
                            disabled={loading}
                            className="w-full py-2 px-4 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 disabled:opacity-50"
                        >
                            {loading ? 'Đang gửi...' : 'Gửi đơn'}
                        </button>
                    </div>
                </form>
            </Card>
        </div>
    );
};

export default Xinnghiphep;