import React, { useState, useEffect } from 'react';
import { apiService } from '../../services/api';

import { useAuth } from '../../contexts/AuthContext';

const CompanySelector = ({ onSelect, onBack, type }) => {
  const [companies, setCompanies] = useState([]);
  const [selectedCompany, setSelectedCompany] = useState(null);
  const [loading, setLoading] = useState(true);
  const [logs, setLogs] = useState([]);
    
  const { user, logout } = useAuth();
  // Hàm utility để log
 

  useEffect(() => {
    const fetchCompanies = async () => {
      try {
        setLoading(true);
        
        const response = await apiService.request('KB_CONGTY', 'Find', {}, {
        //   Selector: `Filter(KB_CONGTY, in( ${user['Họ và Tên']} , [Nhân viên]))`
        });
        
        // Chuyển đổi dữ liệu từ API thành mảng companies
        if (Array.isArray(response)) {
          setCompanies(response);
         
        } else {
          
        }
      } catch (error) {
        
        console.error('Error fetching companies:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchCompanies();
  }, []);

  const handleCompanySelect = (company) => {
    setSelectedCompany(company);
   
  };

  const handleContinue = () => {
    if (selectedCompany) {
     
      onSelect(selectedCompany);
    }
  };

  if (loading) {
    return (
      <div className="text-center py-4">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
        <p className="mt-2">Đang tải danh sách công ty...</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-medium text-gray-900">
        Chọn công ty {type === 'in' ? 'vào' : 'ra'}
      </h3>
      
      <div className="grid gap-2 max-h-96 overflow-y-auto">
        {companies.map((company) => (
          <button
            key={company.ID_congty}
            onClick={() => handleCompanySelect(company)}
            className={`w-full px-4 py-3 text-left border rounded-md hover:bg-blue-50 hover:border-blue-500 transition-colors ${
              selectedCompany?.ID_congty === company.ID_congty
                ? 'border-blue-500 bg-blue-50'
                : 'border-gray-300'
            }`}
          >
            <div className="font-medium">{company['Tên công ty']}</div>
            <div className="text-sm text-gray-500 mt-1">{company['Địa chỉ']}</div>
          </button>
        ))}
      </div>

    
      <div className="flex space-x-3 mt-6">
        <button
          onClick={() => {
          
            onBack();
          }}
          className="flex-1 py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Quay lại
        </button>
        <button
          onClick={handleContinue}
          disabled={!selectedCompany}
          className={`flex-1 py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white ${
            selectedCompany
              ? 'bg-blue-600 hover:bg-blue-700'
              : 'bg-blue-300 cursor-not-allowed'
          }`}
        >
          Tiếp tục
        </button>
      </div>
    </div>
  );
};

export default CompanySelector;