import React, { useState } from 'react';
import { useCamera } from '../../hooks/useCamera';
import { getLocation } from '../../utils/location';
import { imageService } from '../../services/api';
import moment from 'moment-timezone';

export const CameraCapture = ({ onCapture, onBack, type }) => {
  const { videoRef, error, switchCamera, facingMode } = useCamera();
  const [note, setNote] = useState('');
  const [capturing, setCapturing] = useState(false);

  const handleResetPage = () => {
    window.location.reload();
  };
  const handleCapture = async () => {
    if (!videoRef.current || capturing) return;
    
    setCapturing(true);
    try {
      const canvas = document.createElement('canvas');
      const video = videoRef.current;
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      const ctx = canvas.getContext('2d');

      // Handle mirroring for front camera
      if (facingMode === 'user') {
        ctx.scale(-1, 1);
        ctx.translate(-canvas.width, 0);
      }

      ctx.drawImage(video, 0, 0);

      // Add overlay information
      const location = await getLocation();
      const currentTime = moment().tz('Asia/Ho_Chi_Minh');
      const timestamp = currentTime.format('YYYY-MM-DD HH:mm:ss');

      ctx.fillStyle = 'rgba(0, 0, 0, 0.5)';
      ctx.fillRect(10, 10, canvas.width - 20, 80);
      ctx.fillStyle = 'white';
      ctx.font = '14px Arial';

      ctx.fillText(`Vị trí: ${location.latitude}, ${location.longitude}`, 20, 30);
      ctx.fillText(`Thời gian: ${timestamp}`, 20, 50);

      const imageData = canvas.toDataURL('image/jpeg', 0.8);
      const uploadedUrl = await imageService.uploadToImgBB(imageData);

      onCapture({
        imageUrl: uploadedUrl,
        note,
        location,
        timestamp: currentTime.toISOString(),
      });
    } catch (err) {
      console.error('Capture error:', err);
    } finally {
      setCapturing(false);
    }
  };

  return (
    <div className="space-y-6">
      {error && (
        <div className="bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded">
          {error}
        </div>
      )}

      <div className="relative rounded-lg overflow-hidden">
        <video
          ref={videoRef}
          autoPlay
          playsInline
          className={`w-full ${facingMode === 'user' ? 'scale-x-[-1]' : ''}`}
        />
      </div>

      <div>
        <input
          type="text"
          value={note}
          onChange={(e) => setNote(e.target.value)}
          placeholder="Nhập ghi chú"
          className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm"
        />
      </div>

      <div className="grid grid-cols-2 gap-4">
        <button
          onClick={switchCamera}
          className="py-2 px-4 bg-gray-600 text-white rounded-md hover:bg-gray-700"
        >
          Thử lại
        </button>
        <button
          onClick={handleCapture}
          disabled={capturing}
          className="py-2 px-4 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50"
        >
          {capturing ? 'Đang xử lý...' : `Chụp ảnh ${type === 'in' ? 'vào' : 'ra'}`}
        </button>
      </div>

      <button
        onClick={onBack}
        className="w-full py-2 px-4 bg-gray-600 text-white rounded-md hover:bg-gray-700"
      >
        Quay lại
      </button>
      {/* Add this before the logout button */}
<button
    onClick={handleResetPage}
    className="w-full flex justify-center py-3 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-gray-600 hover:bg-gray-700"
>
    <span className="flex items-center">
        <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
        </svg>
        Làm mới trang
    </span>
</button>
    </div>
  );
};