import React, { useState, useEffect } from 'react';
import { apiService } from '../../services/api';
import { useAuth } from '../../contexts/AuthContext';
import { useToast } from '../shared/Toast';
import { useLoading } from '../shared/Loading';
import moment from 'moment-timezone';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

export const AdminAttendanceHistory = ({ onBack }) => {
    const { user } = useAuth();
    const { showToast } = useToast();
    const { setLoading } = useLoading();
    const [history, setHistory] = useState([]);
    const [selectedRecord, setSelectedRecord] = useState(null);
    const [filter, setFilter] = useState('currentMonth');
    const [selectedEmployee, setSelectedEmployee] = useState('all');
    const [employees, setEmployees] = useState([]);
    const [summaryStats, setSummaryStats] = useState(null);
    const [imageLoading, setImageLoading] = useState(true);

    useEffect(() => {
        loadEmployees();
        loadHistory();
    }, [filter, selectedEmployee]);
    const handleResetPage = () => {
        window.location.reload();
    };
    const getImageUrl = (imageUrl) => {
        if (!imageUrl) return null;

        if (imageUrl.startsWith('http://') || imageUrl.startsWith('https://')) {
            return imageUrl;
        }

        if (imageUrl.includes('LICH CV_Images/')) {
            const appName = "WOWSHRMLVSHCNS-CLT-903564153"; 
            const tableName = "LICH CV";
            return `https://www.appsheet.com/template/gettablefileurl?appName=${encodeURIComponent(appName)}&tableName=${encodeURIComponent(tableName)}&fileName=${encodeURIComponent(imageUrl)}`;
        }

        return null;
    };

    const loadEmployees = async () => {
        try {
            const response = await apiService.request('LICH CV', 'Find');
            if (response && Array.isArray(response)) {
                const uniqueEmployees = [...new Set(response.map(record => record['HỌ VÀ TÊN']))];
                setEmployees(uniqueEmployees.sort());
            }
        } catch (error) {
            console.error('Load employees error:', error);
            showToast('Không thể tải danh sách nhân viên', 'error');
        }
    };

    const loadHistory = async () => {
        setLoading({ isLoading: true, message: 'Đang tải dữ liệu...' });
        try {
            let selector = '';

            if (selectedEmployee !== 'all') {
                selector = `Filter(LICH CV,[HỌ VÀ TÊN]="${selectedEmployee}")`;
            }

            if (filter === 'currentMonth') {
                const startOfMonth = moment().startOf('month').format('YYYY-MM-DD');
                const endOfMonth = moment().endOf('month').format('YYYY-MM-DD');
                selector = selectedEmployee === 'all' 
                    ? `Filter(LICH CV,and([NGÀY]>="${startOfMonth}",[NGÀY]<="${endOfMonth}"))`
                    : `Filter(LICH CV,and([HỌ VÀ TÊN]="${selectedEmployee}",[NGÀY]>="${startOfMonth}",[NGÀY]<="${endOfMonth}"))`;
            } else if (filter === 'lastMonth') {
                const startOfLastMonth = moment().subtract(1, 'month').startOf('month').format('YYYY-MM-DD');
                const endOfLastMonth = moment().subtract(1, 'month').endOf('month').format('YYYY-MM-DD');
                selector = selectedEmployee === 'all'
                    ? `Filter(LICH CV,and([NGÀY]>="${startOfLastMonth}",[NGÀY]<="${endOfLastMonth}"))`
                    : `Filter(LICH CV,and([HỌ VÀ TÊN]="${selectedEmployee}",[NGÀY]>="${startOfLastMonth}",[NGÀY]<="${endOfLastMonth}"))`;
            }

            const response = await apiService.request('LICH CV', 'Find', {}, {
                Selector: selector
            });

            if (response && Array.isArray(response)) {
                const sortedHistory = response.sort((a, b) => moment(b.NGÀY).diff(moment(a.NGÀY)));
                setHistory(sortedHistory);
                const stats = calculateStats(sortedHistory);
                setSummaryStats(stats);
            }
        } catch (error) {
            console.error('Load history error:', error);
            showToast('Không thể tải lịch sử chấm công', 'error');
        } finally {
            setLoading({ isLoading: false, message: '' });
        }
    };

    const calculateStats = (data) => {
        try {
            const stats = {
                totalEmployees: new Set(data.map(record => record['HỌ VÀ TÊN'])).size,
                totalDays: data.length,
                totalHours: 0,
                avgHoursPerDay: 0,
                daysWithBothChecks: 0,
                daysWithOnlyIn: 0,
                daysWithOnlyOut: 0,
                averageStartTime: null,
                averageEndTime: null,
                longestDay: 0,
                shortestDay: Infinity,
                totalInTimeCount: 0,
                totalWorkDays: 0
            };

            let totalStartMinutes = 0;
            let totalEndMinutes = 0;
            let startTimeCount = 0;
            let endTimeCount = 0;

            data.forEach(record => {
                const workHours = parseFloat(record['Số giờ làm thực tế'] || 0);
                if (!isNaN(workHours) && workHours > 0) {
                    stats.totalHours += workHours;
                    stats.totalWorkDays++;

                    if (workHours > stats.longestDay) stats.longestDay = workHours;
                    if (workHours < stats.shortestDay) stats.shortestDay = workHours;
                }

                if (record.IN && record.OUT) {
                    stats.daysWithBothChecks++;
                } else if (record.IN) {
                    stats.daysWithOnlyIn++;
                } else if (record.OUT) {
                    stats.daysWithOnlyOut++;
                }

                if (record.IN) {
                    const startTime = moment(record.IN, 'HH:mm:ss');
                    totalStartMinutes += startTime.hours() * 60 + startTime.minutes();
                    startTimeCount++;
                }

                if (record.OUT) {
                    const endTime = moment(record.OUT, 'HH:mm:ss');
                    totalEndMinutes += endTime.hours() * 60 + endTime.minutes();
                    endTimeCount++;
                }
            });

            stats.avgHoursPerDay = stats.totalWorkDays ? stats.totalHours / stats.totalWorkDays : 0;

            if (startTimeCount > 0) {
                const avgStartMinutes = Math.round(totalStartMinutes / startTimeCount);
                stats.averageStartTime = moment()
                    .hours(Math.floor(avgStartMinutes / 60))
                    .minutes(avgStartMinutes % 60)
                    .format('HH:mm');
            }

            if (endTimeCount > 0) {
                const avgEndMinutes = Math.round(totalEndMinutes / endTimeCount);
                stats.averageEndTime = moment()
                    .hours(Math.floor(avgEndMinutes / 60))
                    .minutes(avgEndMinutes % 60)
                    .format('HH:mm');
            }

            stats.totalHours = Number(stats.totalHours.toFixed(2));
            stats.avgHoursPerDay = Number(stats.avgHoursPerDay.toFixed(2));
            stats.longestDay = Number(stats.longestDay.toFixed(2));
            stats.shortestDay = stats.shortestDay === Infinity ? 0 : Number(stats.shortestDay.toFixed(2));

            return stats;
        } catch (error) {
            console.error('Error calculating stats:', error);
            return null;
        }
    };

    const prepareChartData = (data) => {
        try {
            const groupedData = data.reduce((acc, record) => {
                const date = moment(record.NGÀY).format('DD/MM');
                if (!acc[date]) {
                    acc[date] = {
                        date,
                        hoursWorked: 0,
                        employeeCount: new Set(),
                    };
                }
                const hours = parseFloat(record['Số giờ làm thực tế'] || 0);
                if (!isNaN(hours)) {
                    acc[date].hoursWorked += hours;
                }
                acc[date].employeeCount.add(record['HỌ VÀ TÊN']);
                return acc;
            }, {});

            return Object.values(groupedData).map(item => ({
                ...item,
                employeeCount: item.employeeCount.size,
            })).reverse();
        } catch (error) {
            console.error('Error preparing chart data:', error);
            return [];
        }
    };

    const CustomTooltip = ({ active, payload, label }) => {
        if (active && payload && payload.length) {
            return (
                <div className="bg-white p-3 border rounded-lg shadow-lg">
                    <p className="font-medium mb-1">Ngày: {label}</p>
                    <p className="text-sm text-gray-600">
                        Tổng số giờ: <span className="font-medium">{payload[0].value.toFixed(1)}h</span>
                    </p>
                    <p className="text-sm text-gray-600">
                        Số nhân viên: <span className="font-medium">{payload[1].value}</span>
                    </p>
                </div>
            );
        }
        return null;
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <h2 className="text-xl font-bold">Quản lý chấm công</h2>
                <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto">
                    <select
                        value={selectedEmployee}
                        onChange={(e) => setSelectedEmployee(e.target.value)}
                        className="px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                    >
                        <option value="all">Tất cả nhân viên</option>
                        {employees.map(emp => (
                            <option key={emp} value={emp}>{emp}</option>
                        ))}
                    </select>
                    <select
                        value={filter}
                        onChange={(e) => setFilter(e.target.value)}
                        className="px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                    >
                        <option value="currentMonth">Tháng này</option>
                        <option value="lastMonth">Tháng trước</option>
                        <option value="all">Tất cả</option>
                    </select>
                    <button
                        onClick={onBack}
                        className="px-4 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700"
                    >
                        Quay lại
                    </button>
                </div>
            </div>

            {summaryStats && (
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
                        <h3 className="text-lg font-semibold mb-3">Tổng quan</h3>
                        <div className="space-y-2">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Số nhân viên:</span>
                                <span className="font-medium">{summaryStats.totalEmployees}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Tổng ngày có chấm công:</span>
                                <span className="font-medium">{summaryStats.totalDays} ngày</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Tổng giờ làm:</span>
                                <span className="font-medium">{summaryStats.totalHours}h</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Trung bình/ngày làm:</span>
                                <span className="font-medium">{summaryStats.avgHoursPerDay}h</span>
                            </div>
                        </div>
                    </div>

                    <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
                        <h3 className="text-lg font-semibold mb-3">Thời gian trung bình</h3>
                        <div className="space-y-2">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Giờ vào trung bình:</span>
                                <span className="font-medium">{summaryStats.averageStartTime || '-'}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Giờ ra trung bình:</span>
                                <span className="font-medium">{summaryStats.averageEndTime || '-'}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Ngày dài nhất:</span>
                                <span className="font-medium">{summaryStats.longestDay}h</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Ngày ngắn nhất:</span>
                                <span className="font-medium">{summaryStats.shortestDay}h</span>
                            </div>
                        </div>
                    </div>

                    <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
                        <h3 className="text-lg font-semibold mb-3">Chi tiết chấm công</h3>
                        <div className="space-y-2">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Đủ in/out:</span>
                                <span className="font-medium text-green-600">{summaryStats.daysWithBothChecks} ngày</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Chỉ check in:</span>
                                <span className="font-medium text-orange-600">{summaryStats.daysWithOnlyIn} ngày</span>
                            </div>
                            <div className="flex justify-between">
                            <span className="text-gray-600">Chỉ check out:</span>
                               <span className="font-medium text-orange-600">{summaryStats.daysWithOnlyOut} ngày</span>
                           </div>
                       </div>
                   </div>
               </div>
           )}

           <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
               <h3 className="text-lg font-semibold mb-4">Biểu đồ thống kê</h3>
               <div className="h-80">
                   <ResponsiveContainer width="100%" height="100%">
                       <BarChart data={prepareChartData(history)}>
                           <CartesianGrid strokeDasharray="3 3" />
                           <XAxis dataKey="date" />
                           <YAxis yAxisId="left" />
                           <YAxis yAxisId="right" orientation="right" />
                           <Tooltip content={<CustomTooltip />} />
                           <Legend />
                           <Bar yAxisId="left" dataKey="hoursWorked" name="Tổng số giờ làm việc" fill="#3B82F6" />
                           <Bar yAxisId="right" dataKey="employeeCount" name="Số nhân viên" fill="#10B981" />
                       </BarChart>
                   </ResponsiveContainer>
               </div>
           </div>

           <div className="space-y-4">
               {history.length > 0 ? (
                   history.map((record) => (
                       <div
                           key={record['ID CV']}
                           className="bg-white rounded-lg shadow-sm border border-gray-200 p-4 space-y-3"
                       >
                           <div className="flex justify-between items-start">
                               <div>
                                   <div className="font-medium text-blue-600">
                                       {record['HỌ VÀ TÊN']}
                                   </div>
                                   <div className="text-sm text-gray-600">
                                       {moment(record.NGÀY).format('DD/MM/YYYY')}
                                   </div>
                               </div>
                               <span className={`px-2 py-1 text-sm rounded-full ${
                                   record['Số giờ làm thực tế'] >= 8
                                       ? 'bg-green-100 text-green-800'
                                       : 'bg-orange-100 text-orange-800'
                               }`}>
                                   {record['Số giờ làm thực tế'] || 0}h
                               </span>
                           </div>

                           <div className="grid grid-cols-2 gap-4 py-2 border-t border-b border-gray-100">
                               <div>
                                   <div className="text-sm text-gray-500">Check in</div>
                                   <div className="text-sm font-medium flex items-center gap-1 mt-0.5">
                                       <svg className="w-4 h-4 text-green-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                           <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 10l7-7m0 0l7 7m-7-7v18" />
                                       </svg>
                                       {record.IN || '-'}
                                   </div>
                               </div>
                               <div>
                                   <div className="text-sm text-gray-500">Check out</div>
                                   <div className="text-sm font-medium flex items-center gap-1 mt-0.5">
                                       <svg className="w-4 h-4 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                           <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 14l-7 7m0 0l-7-7m7 7V3" />
                                       </svg>
                                       {record.OUT || '-'}
                                   </div>
                               </div>
                           </div>

                           <div className="flex justify-between items-center">
                               <div className="text-sm text-gray-500 truncate flex-1">
                                   {record['GHI CHÚ'] ? (
                                       <div className="flex items-center gap-1">
                                           <svg className="w-4 h-4 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                               <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z" />
                                           </svg>
                                           <span className="truncate">{record['GHI CHÚ']}</span>
                                       </div>
                                   ) : '-'}
                               </div>
                               <button
                                   onClick={() => setSelectedRecord(record)}
                                   className="ml-2 flex items-center text-sm text-blue-600 font-medium"
                               >
                                   Chi tiết
                                   <svg className="w-4 h-4 ml-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                       <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7" />
                                   </svg>
                               </button>
                           </div>

                           <div className="flex flex-wrap gap-2">
                               {record.IN && record.OUT && (
                                   <span className="px-2 py-0.5 text-xs bg-green-50 text-green-700 rounded-full">
                                       Đã hoàn thành
                                   </span>
                               )}
                               {!record.IN && record.OUT && (
                                   <span className="px-2 py-0.5 text-xs bg-orange-50 text-orange-700 rounded-full">
                                       Thiếu check in
                                   </span>
                               )}
                               {record.IN && !record.OUT && (
                                   <span className="px-2 py-0.5 text-xs bg-orange-50 text-orange-700 rounded-full">
                                       Thiếu check out
                                   </span>
                               )}
                           </div>
                       </div>
                   ))
               ) : (
                   <div className="text-center py-8 bg-white rounded-lg shadow">
                       <div className="flex flex-col items-center">
                           <svg className="w-12 h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                               <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                           </svg>
                           <h3 className="mt-2 text-sm font-medium text-gray-900">Không có dữ liệu</h3>
                           <p className="mt-1 text-sm text-gray-500">Chưa có bản ghi chấm công nào trong khoảng thời gian này.</p>
                       </div>
                   </div>
               )}
           </div>

           {selectedRecord && (
               <div className="fixed inset-0 z-50 overflow-y-auto">
                   <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
                       <div
                           className="fixed inset-0 bg-gray-500 bg-opacity-75 backdrop-blur-sm transition-opacity"
                           onClick={() => setSelectedRecord(null)}
                       />

                       <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
                           <div className="p-6">
                               <div className="flex justify-between items-start mb-4">
                                   <h3 className="text-lg font-medium text-gray-900">
                                       Chi tiết chấm công
                                   </h3>
                                   <button
                                       onClick={() => setSelectedRecord(null)}
                                       className="text-gray-400 hover:text-gray-500"
                                   >
                                       <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                           <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                                       </svg>
                                   </button>
                               </div>

                               <div className="space-y-4">
                                   <div className="grid grid-cols-2 gap-4">
                                       <div>
                                           <span className="text-gray-500">Nhân viên:</span>
                                           <span className="ml-2 font-medium text-blue-600">
                                               {selectedRecord['HỌ VÀ TÊN']}
                                           </span>
                                       </div>
                                       <div>
                                           <span className="text-gray-500">Ngày:</span>
                                           <span className="ml-2 font-medium">
                                               {moment(selectedRecord.NGÀY).format('DD/MM/YYYY')}
                                           </span>
                                       </div>
                                   </div>

                                   <div className="grid grid-cols-2 gap-4">
                                       <div>
                                           <span className="text-gray-500">Check in:</span>
                                           <span className="ml-2 font-medium">{selectedRecord.IN || '-'}</span>
                                       </div>
                                       <div>
                                           <span className="text-gray-500">Check out:</span>
                                           <span className="ml-2 font-medium">{selectedRecord.OUT || '-'}</span>
                                       </div>
                                   </div>

                                   <div className="flex justify-between items-center">
                                       <span className="text-gray-500">Số giờ làm việc:</span>
                                       <span className={`px-2 py-1 rounded-full ${
                                           selectedRecord['Số giờ làm thực tế'] >= 8
                                               ? 'bg-green-100 text-green-800'
                                               : 'bg-orange-100 text-orange-800'
                                       }`}>
                                           {selectedRecord['Số giờ làm thực tế'] || 0}h
                                       </span>
                                   </div>

                                   <div className="grid sm:grid-cols-2 gap-4">
                                       {selectedRecord['HÌNH ẢNH IN'] && (
                                           <div className="space-y-2">
                                               <div className="font-medium">Hình ảnh check in</div>
                                               <img
                                                   src={getImageUrl(selectedRecord['HÌNH ẢNH IN'])}
                                                   alt="Check in"
                                                   className="w-full h-40 object-cover rounded-lg"
                                                   onError={(e) => {
                                                       e.target.onerror = null;
                                                       e.target.src = 'path/to/fallback-image.jpg';
                                                   }}
                                               />
                                               {selectedRecord['ĐỊA CHỈ IN'] && (
                                                   <p className="text-sm text-gray-500">
                                                       Vị trí: {selectedRecord['ĐỊA CHỈ IN']}
                                                   </p>
                                               )}
                                           </div>
                                       )}

                                       {selectedRecord['HÌNH ẢNH OUT'] && (
                                           <div className="space-y-2">
                                               <div className="font-medium">Hình ảnh check out</div>
                                               <img
                                                   src={getImageUrl(selectedRecord['HÌNH ẢNH OUT'])}
                                                   alt="Check out"
                                                   className="w-full h-40 object-cover rounded-lg"
                                                   onError={(e) => {
                                                       e.target.onerror = null;
                                                       e.target.src = 'path/to/fallback-image.jpg';
                                                   }}
                                               />
                                               {selectedRecord['ĐỊA CHỈ OUT'] && (
                                                   <p className="text-sm text-gray-500">
                                                       Vị trí: {selectedRecord['ĐỊA CHỈ OUT']}
                                                   </p>
                                               )}
                                           </div>
                                       )}
                                   </div>

                                   {selectedRecord['GHI CHÚ'] && (
                                       <div className="pt-4 border-t">
                                           <h4 className="font-medium mb-2">Ghi chú:</h4>
                                           <p className="text-gray-600">{selectedRecord['GHI CHÚ']}</p>
                                       </div>
                                   )}

                                   {(selectedRecord['LỊCH SỬ IN'] || selectedRecord['LỊCH SỬ OUT']) && (
                                       <div className="pt-4 border-t">
                                           <h4 className="font-medium mb-2">Lịch sử chỉnh sửa:</h4>
                                           <div className="space-y-2 text-sm text-gray-600">
                                               {selectedRecord['LỊCH SỬ IN'] && (
                                                   <div>
                                                       <span className="font-medium">Check in:</span>
                                                       <p className="ml-4">{selectedRecord['LỊCH SỬ IN']}</p>
                                                   </div>
                                               )}
                                               {selectedRecord['LỊCH SỬ OUT'] && (
                                                   <div>
                                                       <span className="font-medium">Check out:</span>
                                                       <p className="ml-4">{selectedRecord['LỊCH SỬ OUT']}</p>
                                                   </div>
                                               )}
                                           </div>
                                       </div>
                                   )}
                               </div>

                               <div className="mt-6">
                                   <button
                                       type="button"
                                       className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                                       onClick={() => setSelectedRecord(null)}
                                   >
                                       Đóng
                                   </button>
                                    {/* Fixed reset button */}
                                    <button
                                        onClick={handleResetPage}
                                        className="fixed bottom-6 right-6 p-3 bg-gray-600 hover:bg-gray-700 text-white rounded-full shadow-lg transition-all duration-300 hover:scale-110 z-50"
                                        title="Làm mới trang"
                                    >
                                        <svg
                                            className="w-6 h-6"
                                            fill="none"
                                            stroke="currentColor"
                                            viewBox="0 0 24 24"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                strokeWidth="2"
                                                d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                                            />
                                        </svg>
                                    </button>
                               </div>
                           </div>
                       </div>
                   </div>
               </div>
           )}
       </div>
   );
};

export default AdminAttendanceHistory;