import axios from 'axios';
import { APP_CONFIG } from '../config/constants';

const apiClient = axios.create({
  baseURL: `${APP_CONFIG.API_BASE_URL}/apps/${APP_CONFIG.APP_ID}/tables`,
  headers: {
    'ApplicationAccessKey': APP_CONFIG.ACCESS_KEY,
    'Content-Type': 'application/json',
  }
});

export const apiService = {
  async request(tableName, action, data = {}, select = {}) {
    try {
      const response = await apiClient.post(`/${tableName}/Action`, {
        Action: action,
        Properties: select,
        ...data
      });
      return response.data;
    } catch (error) {
      console.error('API request failed:', error);
      throw error;
    }
  },

  async login(username, password) {
    return this.request('DSNV', 'Find', {
      Properties: {
        Selector: `Filter(DSNV, and([username] = "${username}", [password] = "${password}"))`
      }
    });
  },

  async checkExistingRecord(idChamCong) {
    return this.request('LICH CV', 'Find', {}, {
      Selector: `Filter(LICH CV,[ID CV]="${idChamCong}")`
    });
  },

  async saveAttendance(data) {
    const action = data.isEdit ? 'Edit' : 'Add';
    return this.request('LICH CV', action, { Rows: [data] });
  }
};

export const imageService = {
  async uploadToImgBB(base64Image) {
    try {
      return await uploadToCloudinary(base64Image);
    } catch (error) {
      console.error('Image upload failed:', error);
      return APP_CONFIG.ERROR_IMAGE_URL;
    }
  }
};
const uploadToCloudinary = async (base64Image) => {
  const CLOUDINARY_CLOUD_NAME = 'dosgc4o7v';
  const CLOUDINARY_UPLOAD_PRESET = 'Cham_Cong_LV';
  
  try {
      const formData = new FormData();
      formData.append('file', base64Image);
      formData.append('upload_preset', CLOUDINARY_UPLOAD_PRESET);
      
      const response = await axios.post(
          `https://api.cloudinary.com/v1_1/${CLOUDINARY_CLOUD_NAME}/image/upload`,
          formData,
          {
              headers: {
                  'Content-Type': 'multipart/form-data'
              }
          }
      );
      
      if (!response.data || !response.data.secure_url) {
          throw new Error('Invalid response from Cloudinary');
      }
      return response.data.secure_url;
  } catch (error) {
      console.error('Cloudinary upload error:', error.response?.data || error);
      throw new Error('Lỗi khi upload ảnh: ' + (error.response?.data?.message || error.message));
  }
};