// LeaveApproval.js
import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { useToast } from '../../components/shared/Toast';
import { useLoading } from '../../components/shared/Loading';
import moment from 'moment-timezone';

export const LeaveApproval = ({ onBack }) => {
    const [leaveRequests, setLeaveRequests] = useState([]);
    const { user } = useAuth();
    const { showToast } = useToast();
    const { setLoading } = useLoading();
    const appId = 'c99c8a08-b6cf-4a71-89e7-2b913c7f45be';
    const accessKey = 'V2-tKXHx-Qri8Z-oevxi-062rn-BGIWj-7eYMM-Uo9nY-USAAM';
    const region = 'www';



    // Load danh sách đơn xin nghỉ phép
    const loadLeaveRequests = async () => {
        setLoading({ isLoading: true, message: 'Đang tải dữ liệu...' });
        try {
            const response = await makeApiRequest('Nghi phep', 'Find', {});
            console.log('API Response:', response);
            setLeaveRequests(response);
        } catch (error) {
            console.error('Error loading leave requests:', error);
            showToast('Không thể tải danh sách đơn nghỉ phép', 'error');
        } finally {
            setLoading({ isLoading: false, message: '' });
        }
    };
    
    useEffect(() => {
        loadLeaveRequests();
    }, []);

    const makeApiRequest = async (tableName, action, data) => {
        const apiUrl = `https://${region}.appsheet.com/api/v2/apps/${appId}/tables/${tableName}/Action`;
        try {
          
    
            const response = await fetch(apiUrl, {
                method: 'POST',
                headers: {
                    'ApplicationAccessKey': accessKey,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    Action: action,
                    Properties: {
                        Locale: 'vi-VN',
                        Timezone: 'Asia/Ho_Chi_Minh',
                    },
                    ...data,
                }),
            });
    
            // Try to get response text first
            const responseText = await response.text();
    
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}, response: ${responseText}`);
            }
    
            // Parse JSON only if we have content
            if (responseText) {
                return JSON.parse(responseText);
            }
            return null;
    
        } catch (error) {
            console.error('API Request Error:', error);
            throw error;
        }
    };
    
    const handleApproveLeave = async (soDon, action) => {
        try {
            setLoading({ isLoading: true, message: 'Đang xử lý...' });
            const status = action === 'approve' ? 'Đã duyệt' : 'Từ chối';
            
            const response = await makeApiRequest('Nghi phep', 'Edit', {
                Rows: [{
                    'SỐ ĐƠN':soDon,
                    'TRẠNG THÁI': status
                }]
            });
    
            // Xử lý cả trường hợp response null/undefined
            if (response !== null && response !== undefined) {
                
            } else {
                // Vẫn coi như thành công nếu không có lỗi
                showToast(`Đơn nghỉ phép đã được cập nhật!`);
            }
            
            await loadLeaveRequests();
        } catch (error) {
            console.error('Error approving leave request:', error);
            showToast('Có lỗi xảy ra khi xử lý đơn: ' + error.message, 'error');
        } finally {
            setLoading({ isLoading: false, message: '' });
        }
    };

    return (
        <div className="p-4 max-w-full">
            <div className="flex flex-col sm:flex-row justify-between items-center mb-6">
                <h2 className="text-xl font-bold text-gray-900 mb-4 sm:mb-0">
                    Duyệt đơn xin nghỉ phép
                </h2>
                <div className="flex space-x-3">
                    <button
                        onClick={loadLeaveRequests}
                        className="px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 text-sm"
                    >
                        <div className="flex items-center">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                            Làm mới
                        </div>
                    </button>
                    <button
                        onClick={onBack}
                        className="px-3 py-2 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300 text-sm"
                    >
                        Quay lại
                    </button>
                </div>
            </div>

            <div className="grid grid-cols-1 gap-4">
                {leaveRequests.map((request) => (
                    <div 
                        key={request['SỐ ĐƠN']}
                        className="bg-white rounded-lg shadow-md p-4 border border-gray-200"
                    >
                        <div className="flex justify-between items-start mb-3">
                            <div className="flex flex-col">
                                <span className="text-lg font-semibold">
                                    {request['Họ và tên']}
                                </span>
                                <span className="text-sm text-gray-500">
                                    Số đơn: {request['SỐ ĐƠN']}
                                </span>
                            </div>
                            <span className={`px-3 py-1 text-xs font-semibold rounded-full
                                ${request['TRẠNG THÁI'] === 'Đã duyệt' ? 'bg-green-100 text-green-800' : 
                                request['TRẠNG THÁI'] === 'Từ chối' ? 'bg-red-100 text-red-800' : 
                                'bg-yellow-100 text-yellow-800'}`}
                            >
                                {request['TRẠNG THÁI'] || 'Phiếu mới'}
                            </span>
                        </div>

                        <div className="space-y-2 text-sm">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Diện nghỉ:</span>
                                <span className="font-medium">{request['DIỆN NGHỈ']}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Thời gian:</span>
                                <span className="font-medium">
                                    {request['NGHỈ TỪ']} - {request['NGHỈ ĐẾN']}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-600">Số giờ nghỉ:</span>
                                <span className="font-medium">{request['Số giờ nghỉ']}</span>
                            </div>
                            <div className="pt-2">
                                <span className="text-gray-600">Lý do:</span>
                                <p className="mt-1 text-gray-800">{request['LÝ DO NGHỈ']}</p>
                            </div>
                        </div>

                        {(!request['TRẠNG THÁI'] || request['TRẠNG THÁI'] === 'Phiếu mới') && (
                            <div className="flex space-x-3 mt-4 pt-3 border-t border-gray-200">
                                <button
                                    onClick={() => handleApproveLeave(request['SỐ ĐƠN'], 'approve')}
                                    className="flex-1 py-2 bg-green-50 text-green-600 rounded-lg hover:bg-green-100 font-medium text-sm"
                                >
                                    Duyệt đơn
                                </button>
                                <button
                                    onClick={() => handleApproveLeave(request['SỐ ĐƠN'], 'reject')}
                                    className="flex-1 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 font-medium text-sm"
                                >
                                    Từ chối
                                </button>
                            </div>
                        )}
                    </div>
                ))}
            </div>

            {leaveRequests.length === 0 && (
                <div className="text-center py-8 text-gray-500">
                    Không có đơn nghỉ phép nào cần duyệt
                </div>
            )}
        </div>
    );
};

export default LeaveApproval;