import React, { useState, useEffect } from 'react';
import moment from 'moment-timezone';
import { useAuth } from '../../contexts/AuthContext';
import { useToast } from '../shared/Toast';
import { clsx } from 'clsx';
import { twMerge } from 'tailwind-merge';

// Utils function
const cn = (...inputs) => {
  return twMerge(clsx(inputs));
};

// Card Components
const Card = React.forwardRef(({ className, ...props }, ref) => (
  <div
    ref={ref}
    className={cn(
      "rounded-lg border bg-white shadow-sm hover:shadow-md transition-shadow",
      className
    )}
    {...props}
  />
));
Card.displayName = "Card";

// Constants for AppSheet API
const APP_IDHCNS = 'c99c8a08-b6cf-4a71-89e7-2b913c7f45be';
const ACCESS_KEYHCNS = 'V2-tKXHx-Qri8Z-oevxi-062rn-BGIWj-7eYMM-Uo9nY-USAAM';
const REGION = 'www';

export const LeaveHistory = ({ onBack }) => {
  const [leaveRecords, setLeaveRecords] = useState([]);
  const [loading, setLoading] = useState(true);
  const { user } = useAuth();
  const { showToast } = useToast();

  useEffect(() => {
    loadLeaveHistory();
  }, []);

  const apiRequest = async (tableName, action, data = {}) => {
    const apiUrl = `https://${REGION}.appsheet.com/api/v2/apps/${APP_IDHCNS}/tables/${tableName}/Action`;
    try {
      const response = await fetch(apiUrl, {
        method: 'POST',
        headers: {
          'ApplicationAccessKey': ACCESS_KEYHCNS,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          Action: action,
          Properties: {
            Locale: 'vi-VN',
            Timezone: 'Asia/Ho_Chi_Minh'
          },
          ...data
        })
      });
      return await response.json();
    } catch (error) {
      throw error;
    }
  };

  const loadLeaveHistory = async () => {
    try {
      setLoading(true);
      const response = await apiRequest('Nghi phep', 'Find');

      const userLeaves = response
        .filter(record => record.KEY === user.KEY)
        .sort((a, b) => moment(b["NGÀY LÀM ĐƠN"], "MM/DD/YYYY").diff(moment(a["NGÀY LÀM ĐƠN"], "MM/DD/YYYY")));

      setLeaveRecords(userLeaves);
    } catch (error) {
      console.error('Failed to load leave history:', error);
      showToast('Không thể tải lịch sử nghỉ phép', 'error');
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (trangThai) => {
    switch (trangThai) {
      case 'Phiếu mới':
        return 'text-blue-600';
      case 'Đã duyệt':
        return 'text-green-600';
      case 'Từ chối':
        return 'text-red-600';
      default:
        return 'text-gray-600';
    }
  };

  return (
    <div className="space-y-4 p-4">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-bold text-gray-900">Lịch sử nghỉ phép</h2>
        <button
          onClick={onBack}
          className="px-4 py-2 bg-gray-600 text-white rounded-md hover:bg-gray-700"
        >
          Quay lại
        </button>
      </div>

      {loading ? (
        <div className="text-center py-4">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto"></div>
          <p className="mt-2 text-gray-600">Đang tải dữ liệu...</p>
        </div>
      ) : leaveRecords.length === 0 ? (
        <Card className="p-4 text-center text-gray-500">
          Không có lịch sử nghỉ phép
        </Card>
      ) : (
        <div className="space-y-3">
          {leaveRecords.map((record, index) => (
            <Card key={index} className="p-4">
              <div className="flex justify-between items-start mb-2">
                <div className="text-base font-medium text-gray-900">
                  {moment(record["NGÀY LÀM ĐƠN"], "MM/DD/YYYY").format("DD/MM/YYYY")}
                </div>
                <div className={`text-sm font-medium ${getStatusColor(record["TRẠNG THÁI"])}`}>
                  {record["Số giờ nghỉ"]}h
                </div>
              </div>

              <div className="flex justify-between items-center mb-2">
                <div>
                  <div className="text-sm text-gray-500">Nghỉ từ - đến</div>
                  <div className="text-base text-gray-900">
                    {moment(record["NGHỈ TỪ"], "MM/DD/YYYY").format("DD/MM/YYYY")} -
                    {moment(record["NGHỈ ĐẾN"], "MM/DD/YYYY").format("DD/MM/YYYY")}
                  </div>
                </div>
                <span className={`text-sm font-medium ${getStatusColor(record["TRẠNG THÁI"])}`}>
                  {record["TRẠNG THÁI"]}
                </span>
              </div>

              <div>
                <div className="text-sm text-gray-500">Số đơn: {record["SỐ ĐƠN"]}</div>
                <div className="text-sm text-gray-500">{record["DIỆN NGHỈ"]}</div>
                {record["LÝ DO NGHỈ"] && (
                  <div className="text-sm text-gray-600 mt-1">
                    {record["LÝ DO NGHỈ"]}
                  </div>
                )}
              </div>

              <div className="flex justify-end mt-2">
                <button className="text-blue-600 text-sm hover:text-blue-800">
                  Chi tiết
                </button>
              </div>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
};

export default LeaveHistory;